@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Roboto:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Roboto:wght@400;700&display=swap");

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

/*  --------------Head----------------- */
.header {
  background-color: white;
  min-width: 375px;
  height: 616px;
  background-image: url(recursos/puntitos.png);
}

@media (min-width: 768px) {
  .header {
    background-image: url(recursos/blanco-puntitos.svg);
  }
}

.header__content {
  min-width: 375px;
  height: 68px;
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  align-items: end;
}

@media (min-width: 768px) {
  .header__content {
    height: 131px;
    padding: 36px 103px 0 80px;
    justify-content: space-between;
    align-items: center;
  }
}

.header__logo {
  width: 54px;
  height: 50px;
}

@media (min-width: 768px) {
  .header__logo {
    width: 95px;
    height: 109px;
  }
}

.header__body {
  min-width: 375px;
  height: 548px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
  gap: 58px;
}

@media (min-width: 768px) {
  .header__body {
    height: 485px;
    display: grid;
    grid-template-columns: 1fr 291px 1fr 380px 1fr;
    justify-content: space-between;
    gap: 0;
  }
}

.header__title {
  width: 291px;
  height: 96px;
  font-family: "Open Sans";
  font-size: 48px;
  font-weight: 700;
  line-height: 48px;
  text-align: center;
  margin: 0;
}

@media (min-width: 768px) {
  .header__title {
    grid-column-start: 2;
    margin-bottom: 200px;
  }
}

.header__img {
  width: 318px;
  height: 300px;
}
@media (min-width: 768px) {
  .header__img {
    grid-column-start: 4;
    width: 380px;
    height: 362px;
    margin-top: 120px;
  }
}

/* --------------boton menu--------------------*/
.buttons {
  display: none;
  width: 428px;
  height: 28px;
}

@media (min-width: 768px) {
  .buttons {
    display: flex;
    justify-content: space-between;
  }
}

.button {
  font-family: "Roboto";
  color: black;
  text-decoration: none;
  font-size: 24px;
  font-weight: 700;
}

@media (min-width: 768px) {
  .menu {
    display: none;
  }
}

.menu__button {
  background-color: transparent;
  width: 36px;
  height: 32px;
  padding: 0;
  margin: 9px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: none;
}

.button__line {
  content: "";
  background: black;
  width: 36px;
  height: 6px;
}

.line-dos {
  margin: 7px 0;
}

.menu__content {
  display: none;
  position: absolute;
  width: 120px;
  background-color: rgb(228, 228, 228);
  font-size: 20px;
  right: 0;
}

.menu:hover .menu__content {
  display: block;
}

.menu__content a {
  display: block;
  color: #000000;
  padding: 5px;
  text-decoration: none;
}

.menu__content a:hover {
  color: #ffffff;
  background-color: #00a4bd;
}

/*  -----------Quien soy-------------------- */

.content__general {
  min-width: 375px;
  height: 100%;
  padding: 55px 32px 68px 32px;
  background-color: #141414;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 768px) {
  .content__general {
    height: 538px;
    flex-direction: row-reverse;
    justify-content: center;
  }
}

.content__text-gral {
  margin: 0;
  padding: 0;
}

@media (min-width: 768px) {
  .content__text-gral {
    margin-left: 90px;
  }
}

.content__title {
  color: white;
  font-family: "Open Sans";
  font-size: 32px;
  font-weight: 700;
  margin: 0;
  padding: 0;
  text-align: center;
}

@media (min-width: 768px) {
  .content__title {
    text-align: start;
  }
}

.content__text {
  color: white;
  font-family: "Roboto";
  font-size: 16px;
  font-weight: 400;
  min-width: 312px;
  margin: 41px 0 57px 0;
  padding: 0;
}

@media (min-width: 768px) {
  .content__text {
    width: 532px;
    font-size: 24px;
    margin: 0;
    margin-top: 28px;
  }
}

.content__logo {
  width: 80px;
  height: 80px;
  margin: 0;
}

@media (min-width: 768px) {
  .content__logo {
    width: 211px;
    height: 211px;
  }
}

/*  -----------Servicios-------------------- */

.sevices__general {
  background-color: white;
  min-width: 375px;
  height: 100%;
  padding: 68px 52px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 43px;
}

@media (min-width: 768px) {
  .sevices__general {
    height: 100%;
    padding: 60px 80px;
  }
}

.services__title {
  font-family: "Open Sans";
  font-size: 32px;
  font-weight: 700;
  margin: 0;
}

.services__total {
  min-width: 271px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 104px;
}

@media (min-width: 768px) {
  .services__total {
    width: 100%;
    height: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-items: start;
    padding: 0;
    margin: 0;
    gap: auto;
  }
}

.services__intern {
  width: 271px;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 14px;
  text-align: center;
}

.intern__subtitle {
  font-family: "Roboto";
  font-size: 24px;
  font-weight: 700;
  margin: 0;
}

.intern__logo {
  width: 138px;
  height: 138px;
}

.intern__text {
  font-family: "Roboto";
  font-size: 16px;
  font-weight: 400;
  margin: 0;
}

/*  -------------Contacto------------------ */

.contact__principal {
  background-color: black;
  min-width: 375px;
  height: 100%;
  padding: 70px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 768px) {
  .contact__principal {
    flex-direction: row;
    justify-content: center;
    gap: 77px;
    align-items: start;
  }
}

.contact__title {
  color: white;
  font-family: "Open Sans";
  font-size: 32px;
  font-weight: 700px;
  margin: 0;
}

@media (min-width: 768px) {
  .contact__title {
    font-size: 48px;
    margin-top: 20px;
  }
}

.contact__form {
  width: 335px;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  .contact__form {
    width: 376px;
  }
}

.contact__label {
  color: white;
  font-family: "Roboto";
  font-size: 13px;
  font-weight: 400;
  margin-top: 21px;
  margin-bottom: 9px;
  padding: 0;
}

.contact__label-name {
  margin-top: 36px;
}

.contact__input {
  height: 45px;
  padding: 0;
  margin: 0;
  font-size: 20px;
}

.contact__message-input {
  height: 113px;
  padding: 0;
  margin: 0;
  font-size: 20px;
}

.contact__button {
  background-color: #ffd953;
  font-family: "Roboto";
  font-size: 16px;
  font-weight: 700;
  width: 100%;
  height: 45px;
  margin-top: 60px;
  border-radius: 4px;
  border: none;
}

/*  ------------footer redes------------------- */
.footer__gral {
  background-color: #dbdbdb;
  height: 493px;
  min-width: 375px;
  margin: 0;
  padding: 53px 0 0 16px;
  display: flex;
  flex-direction: column;
  gap: 85px;
}

@media (min-width: 768px) {
  .footer__gral {
    background-image: url(recursos/gris-puntitos.svg);
    height: 346px;
    flex-direction: row;
    justify-content: space-between;
    gap: auto;
    padding-top: 95px;
    padding-left: 80px;
    padding-right: 215px;
  }
}

.cabecera__text {
  font-family: "Roboto";
  font-size: 13px;
  font-weight: 400;
  margin: 10px 8px;
  font-size: 13px;
  font-weight: 400;
}

@media (min-width: 768px) {
  .cabecera__text {
    margin-left: 15px;
  }
}
.cabecera__logo {
  width: 77px;
  height: 71px;
}
@media (min-width: 768px) {
  .cabecera__logo {
    width: 112px;
    height: 103px;
  }
}

.redes {
  width: 110px;
  margin-left: 3px;
}

@media (min-width: 768px) {
  .redes {
    margin: 0;
  }
}

.redes__logos {
  height: 29px;
  display: flex;
  align-items: center;
}

.redes__linke {
  margin: 25px 0;
}

.redes__title {
  font-family: "Roboto";
  font-size: 16px;
  font-weight: 400;
  display: inline;
  margin: 0;
  margin-left: 8px;
}
